@charset "UTF-8";

.form{
  display: flex;
}

.form input{
  font-size: 1.25rem;
  border: none;
  outline: none;
  border-bottom: 1px solid #50aaff;
  flex:content;
}

.create_btn{
  padding: 0.5rem 1rem;
  margin-left: 1rem;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
  background: #50aaff;
  border: none;
}
.create_btn:hover{
  color: #fff;
  background: #7ec1ff;
}