@charset "UTF-8";

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');
body{
  margin:0;
  padding: 0px;
  background: #f6f6f6;
  font-family: "Noto Sans KR", sans-serif;
}

.todo_list_template{
  width: 90%;
  background: #fff ;
  margin: 4rem auto 0 auto;
  box-shadow: 0px 3px 6px #ccc;
}
.title{
  padding: 2rem;
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  background: #50aaff;
  color: #fff;
}
.form_wrapper{
  padding: 1rem;
  border-bottom: 1px solid #50aaff;
}

.todos_wrapper{
  min-height: 5rem;
}